import { create } from 'zustand';

export type DealStage = 'First Contact' | 'Discovery' | 'Validation' | 'Commitment' | 'Conversion' | 'Won' | 'Lost';

export interface Deal {
  id: string;
  name: string;
  value: number;
  stage: DealStage;
  closeDate: string;
  momentum: number;
  repId: string;
  repName: string;
  company: string;
  urgency: 'Hot' | 'Watch' | 'Cold' | 'Stalled';
  signals: {
    type: 'Engagement' | 'Risk' | 'Momentum Shift' | 'Buying Intent' | 'Inactivity';
    summary: string;
  }[];
  lastActivity: string;
}

interface PipelineState {
  deals: Deal[];
  loading: boolean;
  error: string | null;
  filters: {
    rep: string | null;
    urgency: string | null;
    signalType: string | null;
  };
  sortBy: {
    [key in DealStage]: 'momentum' | 'closeDate' | 'value';
  };
  fetchDeals: () => Promise<void>;
  updateDealStage: (dealId: string, newStage: DealStage) => Promise<void>;
  setFilter: (key: 'rep' | 'urgency' | 'signalType', value: string | null) => void;
  setSortBy: (stage: DealStage, sortBy: 'momentum' | 'closeDate' | 'value') => void;
}

const mockDeals: Deal[] = [
  {
    id: '1',
    name: 'Enterprise SaaS Deal',
    value: 150000,
    stage: 'Discovery',
    closeDate: '2024-03-15',
    momentum: 85,
    repId: 'rep1',
    repName: 'John Doe',
    company: 'Acme Corp',
    urgency: 'Hot',
    signals: [
      { type: 'Buying Intent', summary: 'Multiple stakeholders engaged' },
      { type: 'Momentum Shift', summary: 'Positive sentiment in last call' }
    ],
    lastActivity: '2024-01-20'
  },
  {
    id: '2',
    name: 'Mid-Market Solution',
    value: 75000,
    stage: 'Validation',
    closeDate: '2024-02-28',
    momentum: 72,
    repId: 'rep2',
    repName: 'Jane Smith',
    company: 'Beta Inc',
    urgency: 'Watch',
    signals: [
      { type: 'Engagement', summary: 'Proposal viewed multiple times' }
    ],
    lastActivity: '2024-01-19'
  },
  // Add more mock deals as needed
];

export const usePipelineStore = create<PipelineState>((set, get) => ({
  deals: [],
  loading: false,
  error: null,
  filters: {
    rep: null,
    urgency: null,
    signalType: null
  },
  sortBy: {
    'First Contact': 'momentum',
    'Discovery': 'momentum',
    'Validation': 'momentum',
    'Commitment': 'momentum',
    'Conversion': 'momentum',
    'Won': 'momentum',
    'Lost': 'momentum'
  },
  fetchDeals: async () => {
    set({ loading: true, error: null });
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      set({ deals: mockDeals, loading: false });
    } catch (error: any) {
      set({ error: error.message, loading: false });
    }
  },
  updateDealStage: async (dealId: string, newStage: DealStage) => {
    const currentDeals = get().deals;
    const dealIndex = currentDeals.findIndex(d => d.id === dealId);
    
    if (dealIndex === -1) return;

    // Optimistic update
    const updatedDeals = [...currentDeals];
    updatedDeals[dealIndex] = { ...updatedDeals[dealIndex], stage: newStage };
    set({ deals: updatedDeals });

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 500));
      // If API call succeeds, the optimistic update remains
    } catch (error: any) {
      // If API call fails, revert the optimistic update
      set({ deals: currentDeals, error: error.message });
    }
  },
  setFilter: (key: 'rep' | 'urgency' | 'signalType', value: string | null) => {
    set(state => ({
      filters: {
        ...state.filters,
        [key]: value
      }
    }));
  },
  setSortBy: (stage: DealStage, sortBy: 'momentum' | 'closeDate' | 'value') => {
    set(state => ({
      sortBy: {
        ...state.sortBy,
        [stage]: sortBy
      }
    }));
  }
}));
